'use client';

import { useState } from 'react';
import SuccessModal from './success.modal';
import { ErrorIcon } from '@/assets/svg/svg_icon';

interface WarningModalProps {
    onClose: () => void;
    onDelete: () => void;
}

const WarningModal = ({ onClose, onDelete }: WarningModalProps) => {
    const [showSuccess, setShowSuccess] = useState(false);

    const handleDelete = () => {
        onDelete();
        setShowSuccess(true);
    };

    if (showSuccess) {
        return (
            <SuccessModal
                title="Congratulations"
                description="You have successfully deleted this delivery address"
                onClose={onClose}
            />
        );
    }

    return (
        <div className="fixed bg-black/20 inset-0 flex items-center justify-center p-4 z-50">
            <div className=" p-2 border border-[#FDA106] rounded-3xl bg-transparent">
                <div className="bg-[#FFF4EA] rounded-2xl p-8 max-w-sm w-full text-center">
                    <div className="flex justify-center mb-6">
                        <ErrorIcon />
                    </div>

                    <h3 className="text-xl font-semibold text-gray-900 mb-3">
                        Hold up!
                    </h3>
                    <p className="text-gray-700 mb-5">
                        Are you sure you want to delete? <br />
                        This will permanently remove this address.
                    </p>

                    <div className="flex justify-between gap-5">
                        <button
                            onClick={onClose}
                            className="w-full p-0.5 border backdrop-blur-[10px] border-[#003625] rounded-[18px] transition cursor-pointer">
                            <div className="w-full bg-[#003625] text-white py-3 rounded-[14px] font-semibold">
                                No, Cancel
                            </div>
                        </button>
                        <button
                            onClick={handleDelete}
                            className="w-full p-0.5 border backdrop-blur-[10px] border-[#FDA106] rounded-[18px] transition cursor-pointer">
                            <div className="w-full bg-[#FFF4EA] text-[#FDA106] py-3 rounded-[14px] font-semibold">
                                Yes, Delete
                            </div>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default WarningModal;
