"use client";

import useAppLayoutHook from "@/shared/ui/hooks/app_layout.hook";
import { useEffect, useState } from "react";
import ProfileHeader from "../components/profile_header";
import DeliveryAddressSection from "../components/delivery_address_section";
import EditProfileModal from "../modal/edit_profile";
import { IDeliveryAddress } from "../types/delivery.types";

const ProfilePage = () => {
    const { handleProtectedRoutes } = useAppLayoutHook();
    const [isEditProfileModalOpen, setIsEditProfileModalOpen] = useState(false);

    useEffect(() => {
        handleProtectedRoutes();
    }, [handleProtectedRoutes]);

    const deliveryAddresses: IDeliveryAddress[] = [
        {
            id: 1,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
        {
            id: 2,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
        {
            id: 3,
            name: "Otor John Stephen",
            address: "38 Asheik Jarma Street, Jabi District, Abuja, Federal Capital Territory.",
            phone: "+234 8130000000",
        },
    ];

    return (
        <div className="pb-10">
            {/* Profile Header */}
            <ProfileHeader setIsEditProfileModalOpen={setIsEditProfileModalOpen} />

            {/* Delivery Address Section */}
            <DeliveryAddressSection addresses={deliveryAddresses} />

            {/* Edit Profile Modal */}
            <EditProfileModal
                isOpen={isEditProfileModalOpen}
                onClose={() => setIsEditProfileModalOpen(false)}
            />
        </div>
    );
};

export default ProfilePage;